import { Button, Form, FormInstance, InputNumber, Modal, Space } from 'antd'
import { FC } from 'react'
import { INewTableRecord } from '../CompetitionTableItem'

interface IProps {
  isNumbersModalVisible: boolean
  setIsNumbersModalVisible: (payload: boolean) => void
  onNumbersModalFinish: (values: INewTableRecord) => void
  numbersForm: FormInstance<any>
}

export const TableAddRecordModal: FC<IProps> = ({
  isNumbersModalVisible,
  setIsNumbersModalVisible,
  onNumbersModalFinish,
  numbersForm,
}) => (
  <Modal
    visible={isNumbersModalVisible}
    onCancel={() => setIsNumbersModalVisible(false)}
    footer={null}
  >
    <Form
      form={numbersForm}
      onFinish={onNumbersModalFinish}
      style={{ paddingTop: 10 }}
    >
      <p>Первый участник:</p>
      <Space>
        <Form.Item
          name="memberId0"
          label="Номер участника"
          rules={[{ required: true, message: 'Поле обязательно' }]}
        >
          <InputNumber min={1} />
        </Form.Item>
        <div
          style={{
            position: 'relative',
            top: -12,
            borderRadius: 2,
            background: '#f5222d',
            width: 40,
            height: 30,
            marginLeft: 30,
          }}
        />
      </Space>
      <p>Второй участник:</p>
      <Space>
        <Form.Item
          name="memberId1"
          label="Номер участника"
          rules={[{ required: true, message: 'Поле обязательно' }]}
        >
          <InputNumber min={1} />
        </Form.Item>
        <div
          style={{
            position: 'relative',
            top: -12,
            borderRadius: 2,
            background: '#52c41a',
            width: 40,
            height: 30,
            marginLeft: 30,
          }}
        />
      </Space>
      <p>Третий участник:</p>
      <Space>
        <Form.Item
          name="memberId2"
          label="Номер участника"
          rules={[{ required: true, message: 'Поле обязательно' }]}
        >
          <InputNumber min={1} />
        </Form.Item>
        <div
          style={{
            position: 'relative',
            top: -12,
            borderRadius: 2,
            background: '#096dd9',
            width: 40,
            height: 30,
            marginLeft: 30,
          }}
        />
      </Space>
      <p>Четвертый участник:</p>
      <Space>
        <Form.Item
          name="memberId3"
          label="Номер участника"
          rules={[{ required: true, message: 'Поле обязательно' }]}
        >
          <InputNumber min={1} />
        </Form.Item>
        <div
          style={{
            position: 'relative',
            top: -12,
            borderRadius: 2,
            background: '#fadb14',
            width: 40,
            height: 30,
            marginLeft: 30,
          }}
        />
      </Space>
      <Form.Item>
        <Button type="primary" htmlType="submit">
          Подтвердить
        </Button>
      </Form.Item>
    </Form>
  </Modal>
)
